.footer {
  height: 27vh;
  margin-top: 90px;
  margin-right: 10px;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-container {
  width: 100%;
  height: auto;
  display: flex;
  background: grey;
  border-radius: 5px;
}

.footer-details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-right: 10%;
  margin-left: 10%;
}

.footer-text {
  width: 40%;
  padding: 5px;
}

.footer-text > h2 {
  font-size: 1.4rem;
}

.footer-text > p {
  font-size: 1.1rem;
}

.img {
  width: 250px;
  max-width: 600px;
  height: auto;
  margin-top: -3.5rem;

  vertical-align: center;
}

.links-image {
  width: 150px;
}

.tags {
  background: rgb(196, 221, 35);
  width: 80%;
  height: auto;
  margin-left: 40px;
  margin-top: 40px;
  padding: 20px;
  border-radius: 10px 10px 0 0;
}

.row {
  display: flex;
}

.column {
  width: 25%;
  text-align: center;
}

.column > ul {
  display: flex;
  flex-direction: column;
}
.column > h3 {
  font-size: 1.2rem;
}

a {
  text-decoration: none;
}

@media screen and (max-width: 770px) {
  .links-image {
    width: 120px;
  }
}

@media screen and (max-width: 700px) {
  .footer-container {
    width: 100%;
    height: auto;
  }

  .img {
    width: 180px;
  }
  .links-image {
    width: 100px;
  }

  .footer-text {
    margin-right: 20px;
  }

  .footer-text > h2 {
    font-size: 1.2rem;
  }

  .footer-text > p {
    font-size: 1rem;
  }
}
@media screen and (max-width: 550px) {
  .links-image {
    width: 70px;
  }
  .tags {
    margin-right: 40px;
  }
}

@media screen and (max-width: 500px) {
  .footer-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer-text > h2 {
    font-size: 1.5rem;
  }

  .footer-text > p {
    font-size: 1.2rem;
  }
  .links-image {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;
    width: 120px;
  }
  .tags {
    margin-right: 40px;
  }

  .row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .column {
    width: 100%;
    text-align: center;
  }

  .column > ul {
    display: flex;
    flex-direction: column;
  }
  .column > h3 {
    font-size: 1.7rem;
  }
  .column > p {
    font-size: 1.2rem;
  }
  a {
    text-decoration: none;
    font-size: 1.3rem;
  }
}
